import {useParams} from 'react-router-dom'
// import {useSelector} from 'react-redux'
import {CollectionPreview} from 'components/collection-preview'
import {useQuery} from '@apollo/client'
import {GET_COLLECTIONS_BY_TITLE} from 'gql'

function CollectionPage() {
  const match = useParams('hats')
  const {data} = useQuery(GET_COLLECTIONS_BY_TITLE, {
    variables: {title: match.collectionId},
  })
  const collectionFinded = data?.getCollectionsByTitle
  // const {collections} = useSelector((state) => state.shopReducer)
  // const collectionFinded = collections[match.collectionId]
  return (
    <div>
      <CollectionPreview
        items={collectionFinded?.items}
        title={collectionFinded?.title}
      />
    </div>
  )
}

export {CollectionPage}
