/** @jsxImportSource @emotion/react */
import * as React from 'react'
import {CollectionOverview} from 'components/collection-overview'
import {CollectionPage} from 'components/collection'
import {Routes, Route} from 'react-router-dom'
import {FullPageSpinner} from 'components/lib'
// import {useDispatch, useSelector} from 'react-redux'
// import {fetchShopDataStart} from 'action'
import {GET_COLLECTIONS} from 'gql'
import {useQuery} from '@apollo/client'

function WithShopPage(WrapedComponent) {
  function Component({isLoading, ...props}) {
    return isLoading ? <FullPageSpinner /> : <WrapedComponent {...props} />
  }

  return Component
}

const WithCollectionOverview = WithShopPage(CollectionOverview)
const WithCollectionPage = WithShopPage(CollectionPage)

function ShopPage() {
  // const dispatch = useDispatch()
  const {data, loading: isLoading} = useQuery(GET_COLLECTIONS)
  // const {isLoading} = useSelector((state) => state.shopReducer)
  // React.useEffect(() => {
  //   async function getData() {
  //     dispatch(fetchShopDataStart())
  //   }

  //   getData()
  // }, [dispatch])

  return (
    <div>
      <Routes>
        <Route
          path="/"
          element={
            <WithCollectionOverview
              isLoading={isLoading}
              collections={data?.collections}
            />
          }
        />
        <Route
          path=":collectionId"
          element={<WithCollectionPage isLoading={isLoading} />}
        />
      </Routes>
    </div>
  )
}

export default ShopPage
