/** @jsxImportSource @emotion/react */
import {CollectionItem} from 'components/collection-item'
import {useParams} from 'react-router-dom'
import * as mq from 'styles/media-queries'

function CollectionPreview({title, items}) {
  const match = useParams('hats')

  return (
    <div css={{display: 'flex', flexDirection: 'column'}}>
      <h2
        css={{
          fontSize: '1.75rem',
          textAlign: match.collectionId ? 'center' : 'initial',
        }}
      >
        {title?.toUpperCase()}
      </h2>
      <div
        css={{
          display: 'grid',
          gridTemplateColumns: 'repeat(4, 1fr)',
          gridGap: '4em',
          [mq.small]: {
            gridTemplateColumns: 'repeat(2, 1fr)',
            gridGap: '1em',
          },
          [mq.medium]: {
            gridGap: '2em',
          },
          [mq.iphone5]: {
            gridTemplateColumns: '1fr',
          },
        }}
      >
        {items?.map((collectionItem) => {
          const id = `${title}-${collectionItem.id}`
          return <CollectionItem key={id} item={{...collectionItem, id}} />
        })}
      </div>
    </div>
  )
}

export {CollectionPreview}
